import React, { useState } from 'react'
import MyContactForm from '../components/Contacts/myContactForm'
import { connect } from "react-redux";
import { addNewContactAC, deleteList } from "../redux/home-reducer"
import MyContact from '../components/Contacts/myContact';

const MyContacts = (props) => {
    console.log(props);
    return (
        <div>
            <h1>To Do List</h1>
            <MyContactForm
                addNewContactFunction={props.addNewContactFunc} />
            {
                props.list.map(i => <MyContact delete={props.deleteList} key={i.id} {...i} />)
            }
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        list: state.listPage.list,
        name: state.listPage.name,
    }
}


const mapDispatchToProps = (dispatch) => {
    return {
        addNewContactFunc: (name) => dispatch(addNewContactAC(name)),
        deleteList:(id) => dispatch(deleteList(id))
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(MyContacts)