import { v4 as uuidv4 } from 'uuid';
const ADD_NEW_LIST = "ADD_NEW_LIST";
const CHANGE_NAME = "CHANGE_NAME";
const DELETE_LIST = "DELETE_LIST";


const initialState = {
    list: [
        {
            id: uuidv4(),
            name: "Wake up cheerfully",
        },
        {
            id: uuidv4(),
            name: "Go to the bathroom",
        },
        {
            id: uuidv4(),
            name: "Make bad",
        },
    ],
    name: '',
}

const HomeReducer = (state = initialState, action) => {
    switch (action.type) {

        case ADD_NEW_LIST:
            const newContact = {
                id: uuidv4(),
                name: action.name,
            }
            return {
                ...state,
                list: [...state.list, newContact],
            }
        case CHANGE_NAME:
            return {
                ...state,
                name: action.payload
            }
            case DELETE_LIST: 
            return {
                ...state, 
                list: state.list.filter((item)=> item.id !== action.id )
            }
            
        default: return state

    }
}
export default HomeReducer;
export const addNewContactAC = (name) => ({ type: ADD_NEW_LIST, name});
export const changeNameAC = (payload) => ({type:CHANGE_NAME,payload});
export const deleteList = (id)=> ({type:'DELETE_LIST', id})