import React, { useState } from "react";
import "./myContact.css";


const MyContact = (props) => {
    const deleteLists = () => {
        props.delete(props.id)
    }
    const [check, setCheck] = useState(false)
    const changeCol = (event) => {
        setCheck(event.target.checked)
        
    }

    return (

        <div className={`${check ? 'header-green' : 'header'}`}>
            <input type="checkbox" onChange={changeCol}/>
            <h3>{props.name} </h3>
            <button onClick={deleteLists} type="button">X</button>
        </div>


    )
}

export default MyContact;