import React, { useState } from "react";

const MyContactForm = (props) => {
    const [name, setName] = useState();

    const changeInputName = (event) => {
        setName(event.target.value);    
    }
    const addContact = () => {
        props.addNewContactFunction(name)
    }
return (
    <form>
        <textarea type="text" onChange={changeInputName}></textarea>
        <button onClick={addContact} type="button">Add list</button>
    </form>
)}
export default MyContactForm;